* {
  box-sizing: border-box;
}

body {
  text-align: center;
  color: #333;
  background-color: #f4f4f9;
  font-family: Arial, sans-serif;
}

form {
  text-align: left;
  background: #fff;
  border-radius: 8px;
  padding: 20px;
  display: inline-block;
  box-shadow: 0 4px 6px #0000001a;
}

input, button {
  border: 1px solid #ddd;
  border-radius: 4px;
  width: 100%;
  margin: 10px 0;
  padding: 10px;
  display: block;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  transition: background-color .3s;
}

button:hover {
  background-color: #0056b3;
}

.hidden {
  display: none;
}

.results-bar {
  background-color: #007bff;
  border-radius: 10px;
  height: 20px;
  margin: 10px 0;
  transition: width .5s;
}

.btn-a:hover {
  background-color: #0056b3;
}

.btn-a {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  width: 100%;
  margin: 10px 0;
  padding: 10px;
  transition: background-color .3s;
  display: block;
}

.spinner {
  border: 4px solid #0000001a;
  border-left-color: #09f;
  border-radius: 50%;
  width: 36px;
  height: 36px;
  animation: 1s linear infinite spin;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

#admin-dashboard {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
  position: absolute;
  left: 0;
  right: 0;
}

#results-container {
  flex-direction: column;
  gap: 10px;
  width: 100%;
  margin-top: 10px;
  display: flex;
}

.candidate * {
  margin: 0;
  padding: 0;
}

.candidate {
  background-color: #e6e6fa;
  border-radius: 10px;
  flex-direction: column;
  justify-content: center;
  align-self: center;
  align-items: center;
  gap: 30px;
  width: 80%;
  height: 280px;
  padding-block: 20px;
  display: flex;
}

.candidate img {
  flex: 1;
  width: 140px;
}

.candidate .texts {
  flex-direction: column;
  gap: 5px;
  height: 70px;
  display: flex;
}
